    <h1>{{ User }}</h1>

</template>
<script>
export default {
    name: 'SearchAutocomplete',
    
    props: {
    items: {
      type: Array,
      required: false,
      default: () => [],
    },
  },

    data() {
    return {
    User: '',
      search: '',
      results: [],
      isOpen: false,
    };
  },
  methods: {
    filterResults() {
      this.results = this.items.filter(item => item.toLowerCase().indexOf(this.search.toLowerCase()) > -1);
    },
    onChange() {
        this.filterResults();
      this.isOpen = true;
    }
  }
};
</script>
