<template>
  <div id="tva">
    <div id="ett">
      <h1>Felanmälan</h1>
      <form onsubmit="return false">
        <p>Bifoga bild eller ta foto nedan</p>
        <input
          type="file"
          id="Filinput"
          onchange="document.getElementById('ubild').src = window.URL.createObjectURL(this.files[0])"
        />
        <img id="ubild" width="25%" height="25%" />
        <input
          type="text"
          placeholder="Namn"
          v-model="Namn"
          required
          :class="{ 'input--error': !Namn }"
        />
        <textarea
          type="text"
          contenteditable
          placeholder="Felbeskrivning"
          v-model="Felbeskrivning"
          required
          :class="{ 'input--error': !Felbeskrivning }"
        >
        </textarea>
        <input
          type="text"
          placeholder="Gatuaddress"
          v-model="gatuaddress"
          required
          :class="{ 'input--error': !gatuaddress }"
        />
        <input
          class="Ibox"
          type="text"
          placeholder="Postnummer"
          v-model="Postnummer"
          required
          :class="{ 'input--error': !Postnummer }"
          maxlength="5"
        />
        <input
          class="Ibox"
          type="text"
          placeholder="Ort"
          v-model="Ort"
          required
          :class="{ 'input--error': !Ort }"
        />
        <input
          type="text"
          placeholder="Telefonnummer"
          v-model="Telefonnummer"
          required
          :class="{ 'input--error': !Telefonnummer }"
          maxlength="15"
        />

        <br />
        <br />

        <select v-model="SelectedApi">
          <option disabled value="">Välj API</option>
          <option>Localhost</option>
        </select>
        <br /><br />
        <button @click="postData">Skicka in</button>
      </form>
    </div>
  </div>

  <div id="tre">
    <h3>Dina felanmälningar:</h3>

    <table>
      <tr>
        <th>Nummer</th>
        <th>Felbeskrivning</th>
        <th>Gatuaddress</th>
        <th>Datum</th>
        <th>Postnummer</th>
        <th>Ort</th>
      </tr>
      <tr :key="Issue" v-for="Issue in Issues">
        <template v-if="Issue">
          <td>{{ Issue.id }}</td>
          <td>{{ Issue.description }}</td>
          <td>{{ Issue.address.street }}</td>
          <td>2024</td>
          <td>{{ Issue.address.postalCode }}</td>
          <td>{{ Issue.address.city }}</td>
        </template>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    //ladda issues från storage
    const text = localStorage.getItem("SkapadeIssues");
    const IssueID = text ? JSON.parse(text) : [];
    const Issues = [];
    return {
      SelectedApi: "",
      postResult: null,
      Postnummer: "",
      Ort: "",
      Telefonnummer: "",
      gatuaddress: "",
      Namn: "",
      Felbeskrivning: "",
      IssueID: IssueID,
      Token: "",
      Issues: Issues,
      apiUrls: ["http://localhost:5108/Issues/PostTodoItem"],
    };
  },
  components: {},
  watch: {
    Postnummer() {
      this.Postnummer = this.Postnummer.replace(/\D/g, "");
    },
    Telefonnummer() {
      this.Telefonnummer = this.Telefonnummer.replace(/\D/g, "");
    },
    SelectedApi() {
      if (this.SelectedApi == "Localhost") {
        this.SelectedApi = this.apiUrls[0];
        console.log(this.SelectedApi);
      }
    },
  },
  async mounted() {
    const abc = this.IssueID;
    for (let i = 0; i < abc.length; i++) {
      await fetch("http://localhost:5108/Issues/GetItem/" + abc[i], {
        method: "GET",
        headers: {
          accept: "text/plain",
          "Content-Type": "application/json",
        },
      })
        .then((response) => response.json())
        .then((response) => {
          this.Issues.push(response);
        });
    }
  },

  methods: {
    fortmatResponse(res) {
      return JSON.stringify(res, null, 2);
    },
    //KNAPPEN
    async postData() {
      const postData = {
        description: this.Felbeskrivning,
        yourReferencePhone: this.Telefonnummer,
        address: {
          street: this.gatuaddress,
          postalCode: this.Postnummer,
          city: this.Ort,
        },
        yourReference: this.Namn,
        source: { id: "57c46d4c-77fa-4ded-be1f-d8569baed74a" },
      };
      //Postar textdata
      fetch(this.SelectedApi, {
        method: "POST",
        body: JSON.stringify(postData),
        headers: {
          "Content-type": "application/json; charset=UTF-8",
        },
      })
        .then((response) => response.json())
        .then((response) => {
          console.log(response.id);
          const svar = response.id;
          if (svar) {
            this.IssueID.push(svar);
            localStorage.setItem("SkapadeIssues", JSON.stringify(this.IssueID));
          }
          const fileInput = document.querySelector("#Filinput");
          const formData = new FormData();
          formData.append("files", fileInput.files[0]);
          const options = {
            method: "POST",
            body: formData,
            headers: {
              accept: "*/*",
            },
          };
          //Postar bilden
          fetch("http://localhost:5108/api/Todo/UploadFile", options);
        });
    },
  },
};
</script>
<style>
h3 {
  font-size: calc(15px + 0.5vw);
  margin-top: 0px;
  color: black;
}
h1 {
  margin-top: 0px;
  color: black;
  font-size: calc(15px + 2vw);
}
p {
  color: black;
  font-size: calc(10px + 0.5vw);
}
#tre {
  padding: 2%;
  margin: auto;
  margin-top: 3%;
  width: 50%;
  min-width: 300px;
  max-width: 600px;
  border-radius: 10px;
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  box-shadow: 50px 50px 100px #bababa, -50px -50px 100px #ffffff;
}
#tva {
  padding: 2%;
  margin: auto;
  margin-top: 1%;
  width: 50%;
  min-width: 300px;
  max-width: 600px;
  border-radius: 10px;
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  box-shadow: 50px 50px 100px #bababa, -50px -50px 100px #ffffff;
}
#ett {
  margin: auto;
  max-width: 800px;
  width: 80%;
}

input {
  width: 50%;
  padding: 2% 5%;
  font-weight: 400;
  line-height: 1.5;
  background-clip: padding-box;
  color: black;
  min-width: 200px;
  border-radius: 5px;
  font-size: 15px;
  margin-top: 4px;
  max-width: 400px;
}
textarea {
  resize: vertical;
  width: 50%;
  min-width: 200px;
  max-width: 50%;
  padding: 2% 5%;
  font-weight: 400;
  line-height: 1.5;
  background-clip: padding-box;
  border-radius: 5px;
  color: black;
  font-size: 15px;
  border: none;
  margin-top: 4px;
  border-left: 2px solid green;
}
table {
  margin: auto;
  width: 100%;
  table-layout: auto;
  max-width: 500px;
  word-break: break-all;
  border-collapse: collapse;
  color: black;
  font-size: calc(10px + 0.1vw);
  padding: 2% 5%;
  font-weight: 400;
  line-height: 1.5;
}
td {
  padding-bottom: 2%;
}
.input--error {
  border: none;
  border-left: 2px solid rgb(255, 0, 0);
}
input::file-selector-button {
  padding: 2% 5%;
  font-weight: 400;
  line-height: 1.5;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  appearance: none;
  border-radius: 5px;
  cursor: pointer;
}
img {
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  width: auto;
  max-width: 55%;
}
* {
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  font-weight: normal;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

button {
  padding: 2% 5%;
  font-weight: 400;
  line-height: 1.5;
  border-radius: 5px;
  font-size: 15px;
  margin-top: 4px;
  max-width: 400px;
  background-color: white;
  border: none;
  cursor: pointer;
}
button:disabled {
  opacity: 0.5;
  cursor: auto;
}
input {
  border: none;
  border-left: 2px solid green;
}
::placeholder {
  color: rgb(127, 127, 127);
}
select {
  padding: 2% 5%;
  font-weight: 400;
  line-height: 1.5;
  background-clip: padding-box;
  color: black;
  min-width: 200px;
  border-radius: 5px;
  font-size: 15px;
  margin-top: 4px;
  max-width: 400px;
  border: none;
  background-color: white;
  text-align: center;
}

#Filinput {
  border: none;
}
</style>
